const PasskeyDeviceIcon = ({ defaultIcon, className, size = 'small', }) => {
    let IdIcon = defaultIcon ? defaultIcon : null;
    if (isIPhone()) {
        if (isIPhone8OrEarlier()) {
            IdIcon = jsx(SvgIphoneTouchId, { "data-testid": 'iphone-8-icon' });
        }
        else {
            IdIcon = jsx(SvgFaceId, { "data-testid": 'iphone-icon' });
        }
    }
    if (isAndroid()) {
        const androidVersion = getAndroidVersion();
        if (androidVersion && androidVersion >= 9) {
            IdIcon = jsx(SvgAndroidTouchId, { "data-testid": 'android-9-icon' });
        }
        else {
            IdIcon = jsx(SvgAndroidFaceId, { "data-testid": 'android-icon' });
        }
    }
    if (!IdIcon) {
        return jsx(Fragment, {});
    }
    return (jsx(Icon, { className: className, size: size, children: IdIcon }));
};

export { PasskeyDeviceIcon };
